<template>
  <div id="app">
      <router-view @getFlag="changeFlag" />
      <Tabbar v-show="footerFlag" ></Tabbar>
     
  </div>
</template>

<script>
//1.引入组件
import Tabbar from "./components/Tabbar";


export default {
  data() {
    return {
      footerFlag:true
    }
  },
  mounted() {
      window.addEventListener('unload',this.saveState)
  },
  methods: {
    saveState(){
      localStorage.setItem("state",JSON.stringify(this.$store.state))
    },
    changeFlag(val) {
      this.footerFlag = val;
    }
  },
  components:{
    //2.注册组件
    Tabbar
  },
  watch: {
    "$route.path"(newval){
      if(newval=='/shop'){
        this.changeFlag
      }else if(newval=='/mine/login'){
        this.footerFlag = false;
      }else if(newval=='/index/detail'){
          this.footerFlag = false;
      }else{
        this.footerFlag = true;
      }
    },
  },
};

</script>
